import React, { useState, useEffect } from "react";
import { Form, Button, Col } from "react-bootstrap";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import FormContainer from "../features/FormContainer";
import CheckoutSteps from "../features/CheckoutSteps";
import { savePaymentMethod } from "../../slice/cart-slice";

function PaymentPage() {
	const cart = useSelector((state) => state.cart);
	const { shippingAddress } = cart;

	const dispatch = useDispatch();
	const navigate = useNavigate();

	const [paymentMethod, setPaymentMethod] = useState("PayPal");

	if (!shippingAddress.address) {
		navigate("/shipping");
	}

	const submitHandler = (e) => {
		e.preventDefault();
		dispatch(savePaymentMethod(paymentMethod));
		navigate("/placeorder");
	};

	return (
		<FormContainer>
			<CheckoutSteps step1 step2 step3 />

			<Form onSubmit={submitHandler}>
				<Form.Group>
					<Form.Label as="legend">Select Method</Form.Label>
					<Col className="button py-3">
						<Form.Check
							type="radio"
							label="PayPal or Credit Card"
							id="paypal"
							name="paymentMethod"
							checked
							onChange={(e) => setPaymentMethod(e.target.value)}></Form.Check>
					</Col>
				</Form.Group>

				<Button type="submit" className="button my-2" variant="primary">
					Continue
				</Button>
			</Form>
		</FormContainer>
	);
}

export default PaymentPage;
